import Question1 from '../Question1';
import Adapter from 'enzyme-adapter-react-16';
import {shallow} from 'enzyme';
import * as enzyme from 'enzyme';
import {useNavigation} from '@react-navigation/native';
import {NativeStackNavigationProp} from '@react-navigation/native-stack/lib/typescript/src/types';
import React from 'react';
import {Text} from 'react-native';
import {QuizContext} from './../QuizContext';
enzyme.configure({adapter: new Adapter()});
const navigation = useNavigation<NativeStackNavigationProp<any>>();

jest.mock('@react-navigation/native', () => {
  return {
    useNavigation: jest.fn().mockReturnValue({
      navigate: jest.fn(),
    }),
  };
});

describe('Question 1 component', () => {
  const mockSetScore = jest.fn();
  const mockQuizContext = {
    trackScore: true,
    score: 0,
    setScore: mockSetScore,
    selectedValue: 'ReactJS',
  };
  let wrapper: any;
  beforeEach(() => {
    wrapper = shallow(<Question1 />, {
      context: {QuizContext: mockQuizContext},
    });
    jest.spyOn(React, 'useContext').mockImplementation(() => mockQuizContext);
  });
  afterEach(() => {
    jest.restoreAllMocks();
  });

  it('the component renders properly', () => {
    expect(wrapper).toMatchSnapshot();
  });
  it('should render one question, and 5 answers for that quesiton', () => {
    const questionText = wrapper.find(Text).at(1);
    console.log(questionText);
  });
});
